import SearchLink from '../../components/SearchLink.vue'

export default {
	components: {
		SearchLink
	},
	data(){
		return{
			activeIndex:0,
			categories:[]
		}
	},
	onLoad(){
		this.getCategories()
	},
	methods:{
		changeIndex(index){
			this.activeIndex = index
		},
		// 获取分类数据
		async getCategories(){
			this.categories = await this.$request({
				url:'/api/public/v1/categories'
			})
		},
		// 跳转search_list页面
		toSearchList(catName){
			uni.navigateTo({
				url:'/pages/search_list/search_list?catName='+catName
			})
		}
	}
}
